
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

h1 {
    color: green;
    font-style: italic;
}
/* 
li {
    color: grey;
    padding-top: 3px;
    font-size: 15px;
} 
*/

/* Using element tags like we do above is problematic, as the application might 
 * contain other li / h1 tags in the future. */

/* In HTML we can give elements a class property. Class is a reserved word in js, 
 * so in react, we can give elements a className property. We can then select those 
 * elements by class. */

.note {
    color: grey;
    padding-top: 5px;
    font-size: 15px;
}

.error {
    color: red;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}